export default "<template>\n  <div\n    style=\"\n      display: flex;\n      flex-direction: row;\n      flex-wrap: wrap;\n    \"\n  >\n    <div\n      v-for=\"size in sizes\"\n      :key=\"size\"\n      style=\"margin: 10px; text-align: center;\"\n    >\n      <VIcon\n        name=\"check\"\n        :size=\"size\"\n      />\n      <div>{{ size }}</div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport {\n  VIcon\n} from '@/components';\n\nexport default {\n  name: 'IconSize',\n  components: {\n    VIcon\n  },\n  data () {\n    return {\n      sizes: [\n        '-',\n        'lg',\n        'xs',\n        'sm',\n        '1x',\n        '2x',\n        '3x',\n        '4x',\n        '5x',\n        '6x',\n        '7x',\n        '8x',\n        '9x',\n        '10x'\n      ]\n    };\n  }\n};\n</script>\n";